import React, {useState} from 'react';
import axios from 'axios';
import {FiSearch } from "react-icons/fi"
import {BsSunrise, BsSunset} from "react-icons/bs"
import date from 'date-and-time';
function App() {
  const [data, setData] = useState({})
  const [location, setLocation] = useState('') 
  const months = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'Nocvember',
    'December',
  ]

  const days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday']
  let d = new Date();
  const date = `${days[d.getDay()]} ${d.getDate()} ${months[d.getMonth()]} ${d.getFullYear()}`;
  // const sunrise = new Date(data.sys[0].sunrise * 1000).toLocaleTimeString().slice(0, 4);

  const url = `https://api.openweathermap.org/data/2.5/weather?q=${location}&units=imperial&appid=895284fb2d2c50a520ea537456963d9c`
  // const url = `https://api.openweathermap.org/data/2.5/forecast/hourly?q=${location}&appid=895284fb2d2c50a520ea537456963d9c`
  const searchLocation = (event) => {
   
    if (event.key === 'Enter') {
      axios.get(url).then((response) => {
        setData(response.data)
        console.log(response.data)

      })
      setLocation('')
    }
  }
  return (
    <div className='app'>
       <div className="search">
        <input
          value={location}
          onChange={event => setLocation(event.target.value)}
          onKeyPress={searchLocation}
          placeholder='eg.London'
          type="text" />
          <FiSearch className='search-icon'/>
      </div>
      <div className='container'>
        <div className='top'>
          <div className='location'>
            <p>{data.name},{data.sys?<>{data.sys.country}</>:null}</p>

          </div>
          <div className='temp'>
          {data.main ? <h3>{data.main.temp_max}F</h3> : null}
          </div>
          {data.main ?<p>{date}</p>  : null}
          
          {data.sys?<div className='sunrise'><BsSunrise className="sunrise-logo"/><div className='sunrise-text'>{d = new Date(data.sys.sunrise * 1000).toLocaleTimeString().slice(0, 4)} AM</div></div>:null}
          {data.sys ? <div className='sunset'><BsSunset className="sunset-logo"/><div className='sunset-text'>{d = new Date(data.sys.sunset * 1000).toLocaleTimeString().slice(0, 4)} PM</div></div>:null}
          <div className='description'>
          {data.weather?<img src={`https://openweathermap.org/img/w/${data.weather[0].icon}.png`} alt={data.weather[0].icon} className="weather_icon"/>:null}
          {/* {data.weather ?<div>{data.weather[0].icon}.png</div> : null} */}
          {data.weather ? <p>{data.weather[0].main}</p> : null}
          
          {data.weather ? <p>{data.weather[0].description}</p> : null}
          </div>
          <div className='bottom'>
          <div className='feels'>
            
          {data.main ? <p><p>Feels like</p>{data.main.feels_like}F</p> : null}
          </div>
          <div className='humidity'>
          {data.main ? <p><p>Humidity</p>{data.main.humidity}%</p> : null}
          </div>
          <div className='wind'>
          {data.main ? <p><p>wind Speed</p>{data.wind.speed}MPH</p> : null}
          </div>
          </div>
        </div>
       
          
        
      </div>
    </div>
  );
}
export default App;
